import axios from 'axios';

const BACKEND_URL = 'http://localhost:4000';

export const checkMembership = async (nom, prenom) => {
  try {
    const response = await axios.get(`${BACKEND_URL}/members`, {
      params: { nom, prenom }
    });
    
    return response.data;
  } catch (error) {
    if (error.response && error.response.status === 404) {
      // Membre non trouvÃ©
      return { success: false, isPaid: false };
    }
    throw new Error(error.response?.data?.message || "Erreur de connexion au serveur");
  }
};
